<template>
  <b-col id="AboutSection" cols="10">
    <b-card
      no-body
      class="overflow-hidden"
      header-tag="nav"
      bg-variant="dark"
      text-variant="white"
    >
      <template #header>
        <b-row>
          <b-col cols="12">
            <h1><font-awesome-icon icon="fa-solid fa-id-card" /> About</h1>
          </b-col>
        </b-row>
      </template>

      <b-row no-gutters>
        <b-card-group deck class="mb-2">
          <b-card
            img-src="https://i.redd.it/dzl536txpkj71.jpg"
            img-alt="Card image"
            class="overflow-hidden"
            bg-variant="dark"
            img-height="100%"
            no-body
          >
          </b-card>

          <b-card
            bg-variant="dark"
            text-variant="white"
            header-tag="header"
            style="max-height: 450px; overflow: auto"
          >
            <template #header>
              <b-row>
                <b-col cols="12">
                  <b-nav card-header pills>
                    <b-nav-item :active="tab === 1" @click="switchTab(1)"
                      >Profile</b-nav-item
                    >
                    <b-nav-item :active="tab === 2" @click="switchTab(2)"
                      >Stats</b-nav-item
                    >
                  </b-nav>
                </b-col>
              </b-row>
            </template>

            <component :is="activeComponent" />
          </b-card>
          <!--
          <Stats v-if="activeComponent==='Stats'"></Stats>
          <Profile v-if="activeComponent==='Profile'"></Profile>
              -->
        </b-card-group>
      </b-row>
    </b-card>
  </b-col>
</template>

<script>
import Profile from "./Profile.vue";
import Stats from "./Stats.vue";
export default {
  el: "#AboutSection",
  data() {
    return {
      activeComponent: "Profile",
      tab: 1,
    };
  },
  methods: {
    switchTab(tabNum) {
      if (tabNum === 1) {
        this.activeComponent = "Profile";
      }
      if (tabNum === 2) {
        this.activeComponent = "Stats";
      }
      this.tab = tabNum;
      return this.tab;
    },
  },
  components: { Profile, Stats },
};
</script>

